///
/// From https://tympanus.net/codrops/2016/10/18/inspiration-for-letter-effects/
///

/* Letter Effects */

[class*="letter"] {
	display: block;
	position: relative;
	-webkit-flex: none;
	-ms-flex: none;
	flex: none;
	white-space: pre;
}

.letter-effect {
	display: -webkit-flex;
	display: -ms-flexbox;
	display: flex;
	-webkit-flex-direction: row;
	-ms-flex-direction: row;
	flex-direction: row;
	-webkit-align-items: center;
	-ms-flex-align: center;
	align-items: center;
	-webkit-flex-wrap: wrap;
	-ms-flex-wrap: wrap;
	flex-wrap: wrap
}





.pretitle {
    white-space: nowrap;
    position: absolute;
    top: .5em;
		opacity: 1;
		@include vendor('transition', (
			'opacity #{_duration(on-scroll)} ease-in-out',
			'transform #{_duration(on-scroll)} ease-in-out'
		));
		&.is-inactive {
			@include vendor('transition', (
				'opacity #{_duration(on-scroll)} ease-in-out',
				'transform #{_duration(on-scroll)} ease-in-out'
			));
			@include vendor('transform', 'translateX(-10rem)');
			opacity:0;
			}
}

.title {
/*	@include vendor('transition', 'opacity #{_duration(on-scroll)} ease-in-out'); */
	&.is-inactive {
		opacity:0;
		}
}

.swoop {
	@include vendor('transition', (
		'opacity #{_duration(on-scroll)} ease-in-out',
		'transform #{_duration(on-scroll)} ease-in-out'
	));
	&.is-inactive {
		opacity:0;
		&.up {
		@include vendor('transform', 'translateY(1rem)');
		}
		&.down {
		@include vendor('transform', 'translateY(-1rem)');
		}
		&.left {
		@include vendor('transform', 'translateX(1rem)');
		}
		&.right {
			@include vendor('transform', 'translateX(-1rem)');
		}
	}
}

.ml11 .text-wrapper {
  position: relative;
  display: inline-block;
  padding-top: 0.1em;
  padding-right: 0.05em;
  padding-bottom: 0.15em;
}

.ml11 .line {
  opacity: 0;
  position: absolute;
  left: 0;
  height: 100%;
  width: 3px;
  background-color: #fff;
  transform-origin: 0 50%;
}

.ml11 .line1 {
  top: 0;
  left: 0;
}

.ml11 .letter {
  display: inline-block;
  line-height: 1em;
}
