/*
 *
 * Dusk
 *
 */
@import 'shared';

// mixin to extend dusk filter
// @mixin dusk
// @param $filters... {filter} - Zero to many css filters to be added
// @example
//   img {
//     @include dusk;
//   }
//   or
//   img {
//     @include dusk(blur(2px));
//   }
//   or
//   img {
//     @include dusk(blur(2px)) {
//       /*...*/
//     };
//   }
@mixin dusk($filters...) {
  @include filter-base;
  filter: none $filters;
  background-color: #19547B;

  img {
    filter:grayscale(1);
  }

  &::after {
    background: linear-gradient(45deg, rgba(#FFD89B, 1), rgba(#133F5C, .4));
    mix-blend-mode: overlay;
  }

  @content;
}

// dusk Instagram filter
%dusk,
.dusk {
  @include dusk;
}
